import React, {useState} from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

import './styles/tab.scss'
import {BiSearch} from 'react-icons/bi'
import {Data3} from './data'
import BlogTabItem from './BlogTabItem'

const BlogTab = () => {

    const [search, setSearch] = useState('');

    return (
        <div className='tab-container'>
            <div className='row1' data-aos="fade-left" data-aos-easing="ease-in-sine">
                <p>Discover our blog</p>
                <div className='search-bar'>
                    <button type='button'><BiSearch /></button>
                    <input
                        type='text'
                        name='search'
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                        placeholder='Search Blog'
                    />
                </div>
            </div>
            <Tabs className='gaming-tab-row'>
                    <TabList data-aos="fade-left" data-aos-easing="ease-in-sine">
                            <Tab>News</Tab>
                            <Tab>Personal</Tab>
                            <Tab>Business</Tab>
                            <Tab>For Admins</Tab>
                            <Tab>Product Tips</Tab>
                            <Tab>Online Security</Tab>
                            <Tab>Social</Tab>                           
                    </TabList>
                    <div className="tab-panel">
                            {Data3.map((d, i) => {
                                return (
                                    <TabPanel>
                                        <BlogTabItem key={i} {...d} />
                                    </TabPanel>
                                );
                            })}
                    </div>
                </Tabs>
            
        </div>
    )
}

export default BlogTab
