import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import ReactPaginate from "react-paginate";

import {FaChevronLeft, FaChevronRight} from 'react-icons/fa';
import {BsArrowRight} from 'react-icons/bs'
import {AiOutlineClockCircle} from 'react-icons/ai'
import {VscCircleFilled} from 'react-icons/vsc'
import {Data2} from './data';

const Pagination1 = () => {


    const [items, setItems] = useState(Data2.slice(0, 18));
    const [pageNumber, setPageNumber] = useState(0);

    const itemsPerPage = 6;
    const pagesVisited = pageNumber * itemsPerPage;

    const displayItems = items
    .slice(pagesVisited, pagesVisited + itemsPerPage)
    .map((props) => {
      return (
        <div className='pagiantion-item1'>
            <div className='img-cont'>
                <div className='img' style={{content: `url(${props.img})`}}></div>
            </div>
            <div className='info'>
                <div className='inline'>
                    <span>Business</span>
                    <small><AiOutlineClockCircle /> 1 min read</small>
                </div>
                <h6>{props.title}</h6>
                <p className='p'>{props.description}</p>
                <div className='profile-cont'>
                    <div className='inline2'>
                        <div className='profile' style={{content: `url(${props.profile})`}}></div>
                        <p>by {props.name}</p>
                        <span><VscCircleFilled /></span>
                        <p>25 June 2021</p>
                    </div>
                    <Link to={props.to}>
                        Read More <BsArrowRight />
                    </Link>
                </div>
            </div>
            
        </div>
      );
    });

    const pageCount = Math.ceil(items.length / itemsPerPage);

    const changePage = ({ selected }) => {
      setPageNumber(selected);
    };


    return (
        <div className='courses-pagination'>
             <div className='single-page'>{displayItems}</div>
                <ReactPaginate
                    previousLabel={<FaChevronLeft />}
                    nextLabel={<FaChevronRight />}
                    pageCount={pageCount}
                    onPageChange={changePage}
                    containerClassName={"paginationBttns"}
                    previousLinkClassName={"previousBttn"}
                    nextLinkClassName={"nextBttn"}
                    disabledClassName={"paginationDisabled"}
                    activeClassName={"paginationActive"}
                />
        </div>
    )
}

export default Pagination1